#experience{
    height: auto;
}
.experience__container{
  
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap:2rem;
    
}
.experience__container > div{
background-color: black;
padding: 2.4rem 5rem;
border-radius: 2rem;
border: 5px solid rgba(0, 0, 0, 0.3);
transition: var(--transition);
margin-left: 50px;
margin-right: 50px;
}
.experience__container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.experience__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}
.experience__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}
.experience__details{
    display: flex;
    gap: 1rem;
}
.experience__details-icons{
    margin-top: 6px;
    color: var(--color-primary);
}




@media screen and (max-width:1024px) {
    
   .experience__container{
    grid-template-columns: 1fr;
    
   }
   .experience__frontend{
    height: 100%;
   }
   .experience__content{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
   }
   .experience__container > div{
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
   }
    }
    /*=========== MEDIA QUERY===(SMALL DEVICE)=====================*/
    @media screen and (max-width:600px) {
        .experience__container{
            grid-template-columns: 1fr;
            
           }
           
           .experience__content{
            display: grid;
            grid-template-columns: 1fr 1fr  ;
            gap: 0.2rem;
           }
           .experience__container > div{
            width: 80%;
            padding: 2rem;
            margin: 0 auto;
           }
       
    }
 